.layout {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 30px;
}

.header-right {
  display: flex;
  justify-content: space-between;
}

.header-name {
  margin-left: 30px;
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.settings {
  margin-right: 30px;
  cursor: pointer;
}

.username {
  margin-right: 30px;
  cursor: pointer;
}

.logout-btn {
  padding: 5px 10px;
  margin-right: 30px;
  background: #337ab7;
  border: none;
  color: white;
  font-size: 14px;
  border-radius: 3px;
  cursor: pointer;
}

.container {
  height: 100%;
}